@import url(https://fonts.googleapis.com/css2?family=Rowdies&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #f6f8fa;
  color: #24292e;
  min-height: 100vh;
  -webkit-animation-name: app-background;
          animation-name: app-background;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  font-family: "Rowdies", cursive;
}

.Sidebar {
  min-height: 100%;
  background-color: #b6b6b6;
  position: absolute;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  z-index: 3;
  width: 0%;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  border: black;
  border-width: 0.25rem;
  border-style: solid;
  border-style: none solid none none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes sidebar-open {
  0% {
    width: 0%;
  }
  100% {
    width: 30%;
  }
}

@keyframes sidebar-open {
  0% {
    width: 0%;
  }
  100% {
    width: 30%;
  }
}

@-webkit-keyframes sidebar-close {
  0% {
    width: 30%;
  }
  100% {
    width: 0%;
  }
}

@keyframes sidebar-close {
  0% {
    width: 30%;
  }
  100% {
    width: 0%;
  }
}

.header {
  height: 6rem;
  width: 100%;
  border: black;
  border-width: 0.25rem;
  border-style: none none solid none;
  display: flex;
  overflow: hidden;
}

.header--background {
  position: absolute;
  height: 6rem;
  background: url(/sandbox-site/static/media/space-header.11f225cc.png) repeat-x;
  width: 100%;
  z-index: 1;
}

.header-nav {
  width: 80%;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.header-menu {
  align-items: center;
  display: flex;
  margin-left: 1%;
  cursor: pointer;
  height: 100%;
  z-index: 5;
  fill: white;
}

.header-nav--item {
  font-family: "PlayFair Display";
  font-size: 1.5rem;
  color: white;
  font-weight: 600;
  cursor: pointer;
  -webkit-user-select: none;
}

.header-nav--item-bastion {
  font-family: "Rowdies", cursive;
  font-size: 2rem;
}
.header-nav--item-sunbro {
  font-family: "Rowdies", cursive;
  font-size: 2rem;
}
.header-nav--item-home {
  font-family: "Rowdies", cursive;
  font-size: 2rem;
}
.header-nav--item:hover {
  color: rgb(255, 255, 255);
}

.furi {
  -webkit-animation: furi-animation 3s ease;
          animation: furi-animation 3s ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  margin-top: 5%;
  height: auto;
  max-width: 80%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes furi-animation {
  0% {
    opacity: 0;
    margin-right: 10%;
  }
  30% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  35% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  40% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  45% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  70% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  75% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  100% {
    opacity: 1;
    margin-right: 0rem;
  }
}

@keyframes furi-animation {
  0% {
    opacity: 0;
    margin-right: 10%;
  }
  30% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  35% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  40% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  45% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  70% {
    -webkit-filter: invert(0);
            filter: invert(0);
  }
  75% {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  100% {
    opacity: 1;
    margin-right: 0rem;
  }
}

.home-page--container {
  /* animation: background-color-animation 3s ease; */
  height: 100vh;
}

@-webkit-keyframes background-color-animation {
  30% {
    background-color: white;
  }
  35% {
    background-color: black;
  }
  40% {
    background-color: white;
  }
  45% {
    background-color: black;
  }
  70% {
    background-color: white;
  }
  75% {
    background-color: black;
  }
  100% {
    background-color: #f6f8fa;
  }
}

@keyframes background-color-animation {
  30% {
    background-color: white;
  }
  35% {
    background-color: black;
  }
  40% {
    background-color: white;
  }
  45% {
    background-color: black;
  }
  70% {
    background-color: white;
  }
  75% {
    background-color: black;
  }
  100% {
    background-color: #f6f8fa;
  }
}

.solaire {
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation: solaire-animation-in 1s ease;
          animation: solaire-animation-in 1s ease;
  height: auto;
  max-width: 100%;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes solaire-animation-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes solaire-animation-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes solaire-animation-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
    cursor: default;
  }
}

@keyframes solaire-animation-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
    cursor: default;
  }
}

.upgrade-button {
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  background: url(/sandbox-site/static/media/space-header.11f225cc.png);
  color: #fff;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px rgb(78, 78, 78);
  margin-bottom: 1rem;
}

.upgrade-button:hover {
  background-color: #3e8e41;
}

.upgrade-button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.bastion {
  -webkit-animation-name: bastion-animation;
          animation-name: bastion-animation;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  height: auto;
  max-width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes bastion-animation {
  0% {
    opacity: 0;
    margin-left: 100%;
  }
  100% {
    opacity: 1;
    margin-left: 0rem;
  }
}

@keyframes bastion-animation {
