.bastion {
  animation-name: bastion-animation;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
  animation-duration: 2s;
  height: auto;
  max-width: 100%;
  user-select: none;
}

@keyframes bastion-animation {
  0% {
    opacity: 0;
    margin-left: 100%;
  }
  100% {
    opacity: 1;
    margin-left: 0rem;
  }
}

.button {
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background: url("../../assets/images/space-header.png");
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px rgb(78, 78, 78);
  margin-bottom: 1rem;
}

.button:hover {
  background-color: #3e8e41;
}

.button:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
